import React from 'react'
import drawChartScore from '../utils/chart-score';
import drawChartTimings from '../utils/chart-timings';
import drawChartBytes from '../utils/chart-bytes';
import {dragDrop} from '../utils/drag';
import {idleCallback, scriptAsync, show, hide, readFiles} from '../utils/utils';

// Load the Visualization API and the corechart package.

scriptAsync('www.gstatic.com/charts/loader.js', () => {
    google.charts.load('current', {'packages':['corechart']});
});

class IndexPage extends React.Component {
    constructor(props) {
        super(props);
        this.handleFiles = this.handleFiles.bind(this);
    }

    async handleFiles(files) {
        const read = await readFiles(files);
        idleCallback(() => {
            try {
                hide('#parseError');
                show('#charts');
                console.log(read);
                drawChartScore('chart_score', read);
                drawChartBytes('chart_bytes', read);
                drawChartTimings('chart_timings', read);
            } catch (e) {
                hide('#charts');
                show('#parseError');
                }
        });
    }

    componentDidMount() {
        console.log(this.handleFiles);
        dragDrop('drop-area', this.handleFiles);
    }

    render() {
        return (
            <main>
            <h2>Keep your website under budget</h2>

            <p className="description">
            </p>

            <div id="drop-area">
                <form className="my-form">
                    <p>Select/Drag multiple Lighthouse reports</p>
                    <input type="file" id="fileElem" multiple accept=".json" onChange={(e) => this.handleFiles(e.target.files)}/>
                    <label className="button" htmlFor="fileElem">Select reports</label>
                </form>
            </div>

            <div id="charts">
                <div className="score hidden">
                    <h2 className="score-title">Score</h2>
                    <div id="chart_score"></div>
                    <div id="legend"></div>
                    <div className="flexy">
                        <a id="score__download" className="button" download="score.png">Download</a>
                    </div>
                </div>

                <div className="bytes hidden">
                    <h2 className="chart-bytes">Bytes</h2>
                    <div id="chart_bytes"></div>
                    <div className="flexy">
                        <a id="bytes__download" className="button" download="bytes.png">Download</a>
                    </div>
                </div>

                <div className="timings hidden">
                    <h2 className="chart-timings">Timings</h2>
                    <div id="chart_timings"></div>
                    <div className="flexy">
                        <a id="timings__download" className="button" download="timings.png">Download</a>
                    </div>
                </div>
            </div>

            <div id="parseError" className="error hidden">
                Oops something wrong happened!!
            </div>
        </main>
        )
    }
}

export default IndexPage



